  display: flex;
  flex-direction: column;
  justify-content: center;
  max-height: 50rem;
  min-height: auto;
  width: 18rem;
  min-width: auto;
}
.recipe__title {
  display: flex;
  border-bottom: 0.1rem solid whitesmoke;
  justify-content: space-evenly;
  align-items: flex-end;
}
.recipe__title > h5 {
  font-size: 1.2rem;
}
.recipe__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem 1rem;
}
.recipe__image {
  min-width: 6rem;
  width: auto;
  max-width: calc(100% - 25rem);
}
.recipe__image > img {
  max-height: 100%;
  max-width: 100%;
}
/* Pagination */
.pagination {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 2rem 0;
  margin: 5rem 0;
}
.menu__pages {
  text-align: center;
  font-size: 1.2rem;
}
.page_no {
  display: inline-block;
  text-decoration: none;
  color: rgb(231, 236, 227);
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  background-color: rgba(25, 8, 66, 0.897);
}
.page_no:hover {
  -webkit-transform: translateY(-0.3rem) scale(1.1);
          transform: translateY(-0.3rem) scale(1.1);
}
.page_no:active {
  -webkit-transform: none;
          transform: none;
}
.recipe__button {
  padding: 1.2rem;
  margin: auto;
  margin-bottom: 2rem;
  display: inline-block;
  text-decoration: none;
  color: antiquewhite;
  border-radius: 10%;
  font-size: large;
  background-color: #ca064e;
}
.recipe__button:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
/* FAILED MESSAGES */
.failed__container {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2%;
  margin-bottom: 25%;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}
.goback-button {
  padding: 1.2rem;
  margin: auto;
  margin-bottom: 2rem;
  display: inline-block;
  text-decoration: none;
  color: antiquewhite;
  border-radius: 10%;
  font-size: 2.5rem;
  background-color: #0c8b32;
}
.failed__body {
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(rgba(220, 20, 60, 0.35)),
    to(rgba(255, 0, 0, 0.46))
  );
  background: linear-gradient(
    to right,
    rgba(220, 20, 60, 0.35),
    rgba(255, 0, 0, 0.46)
  );
  margin: auto;
  padding: 2rem;
  border-radius: 2rem;
}
.failed__message {
  font-size: 3rem;
}
.failed-icon {
  color: rosybrown;
  font-size: 5rem;
}
.ingredients {
  text-align: left;
  font-family: "Open Sans", "Comic Sans MS", "Zapf Chancery", Helvetica, Roboto,
    "Proxima Nova", "Sans-Serif";
}
.item-checked {
  list-style: none;
}
.item-checked::before {
  content: "✔ ";
  font-size: 1.2rem;
  font-weight: bolder;
  color: #0c8b32;
}
.not-checked {
  list-style: none;
}
.not-checked::before {
  content: "❌ ";
  font-size: 1.2rem;
  font-weight: bolder;
  color: #ca064e;
}

/*MEDIA for mobiles */
@media only screen and (max-width: 850px) {
  .hamburger-menu {
    display: flex;
    cursor: pointer;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 2rem 2rem auto auto;
    padding: 1rem;
    font-weight: bolder;
    font-size: x-large;
    border: 0.1rem solid white;
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
    border-radius: 5%;
  }
  .hamburger-menu:hover {
    -webkit-transform: translateY(-0.3rem);
            transform: translateY(-0.3rem);
  }
  .hamburger-menu:active {
    -webkit-transform: translateY(0.1rem);
            transform: translateY(0.1rem);
  }
  .menu__items {
    display: none;
    flex-direction: column;
    text-align: center;
    margin: auto;
    margin-top: 0.25rem;
  }
  .item-right {
    justify-content: center;
    margin: auto;
    margin-top: 0.25rem;
    padding: 0;
    flex-direction: column;
  }
  .m-right {
    margin: 0.25rem;
  }
  .color-switch {
    justify-content: center;
    margin: 0.5rem;
  }
}

